<!-- SVG  -->
<script setup lang="ts">
import { computed, type Component } from 'vue'

export interface YzySvgIconProps {
  /**  */
  width?: number | string
  /**  */
  height?: number | string
  /**  */
  color?: string
  /** vite-svg-loader  */
  icon?: Component
}

const props = withDefaults(defineProps<YzySvgIconProps>(), {
  width: 20,
  height: 20,
  color: '#000000',
})

defineSlots<{
  /**  vite-svg-loader  */
  default: () => Component
}>()

const _width = computed(() => parseFloat(props.width + '') + 'px')
const _height = computed(() => parseFloat(props.width + '') + 'px')
</script>

<template>
  <span class="yzy-svg-icon">
    <slot><component :is="icon" /></slot>
  </span>
</template>

<style scoped>
.yzy-svg-icon {
  display: inline-block;
  width: v-bind(_width);
  height: v-bind(_height);
  color: v-bind(color);
}

.yzy-svg-icon svg {
  width: 100%;
  height: 100%;
}

.yzy-svg-icon svg * {
  fill: currentcolor;
}
</style>
